<template>
  <div>
    <textarea-autosize :id="id" v-model="inputValue" class="form-control" :min-height="38" :class="{unfocused: !focused}" @blur.native="focused = false" @focus.native="focused = true" />
  </div>
</template>


<script>
/**
A component which allows multi-line text-input but shows only one line when not focused
 */
export default {
  name: 'RemarkInput',
  props: [ 'value', 'id' ],
  data() {
    return {
        focused: false
    }
  },
  computed: {
    inputValue: {
      get() { return this.value; },
      set(v) { this.$emit('input', v) }
    }
  },
  mounted() {
  },
  watch: {
  },
  methods: {
  }
}
</script>

<style lang="scss" scoped>
    textarea.unfocused { height:38px!important; }
    textarea { transition:height 0.2s; }
</style>